body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.introHead > h1 {
    font-size: 36px;
    letter-spacing: 0.07em;
}

.App-header {
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.common-head {
    margin: 0;
    padding: 13px 0;
}
/* Animation css*/



.common-head span{
    letter-spacing: 15px;
    -webkit-animation: animate 1s infinite linear;
            animation: animate 1s infinite linear;
    font-size: 30px;
}

 @-webkit-keyframes animate{
            0%{
                color: #ed2424;
                text-shadow: none;
            }
            18%{
                color: #ed2424;
                text-shadow: none;
            }
            20%{
                color :#ffffff;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            30%{
                color: #ed2424;
                text-shadow: none;
            }
            70%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            85%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            90%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                
            }
        }

 @keyframes animate{
            0%{
                color: #ed2424;
                text-shadow: none;
            }
            18%{
                color: #ed2424;
                text-shadow: none;
            }
            20%{
                color :#ffffff;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            30%{
                color: #ed2424;
                text-shadow: none;
            }
            70%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            85%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            90%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                
            }
        }


        .common-head span:nth-child(1){
            -webkit-animation-delay: .2s;
                    animation-delay: .2s;
        }
         .common-head span:nth-child(2){
            -webkit-animation-delay: .4s;
                    animation-delay: .4s;
        }
         .common-head span:nth-child(3){
            -webkit-animation-delay: .6s;
                    animation-delay: .6s;
        }
         .common-head span :nth-child(4){
            -webkit-animation-delay: .6s;
                    animation-delay: .6s;
        }
         .common-head span :nth-child(5){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(6){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(7){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(8){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }


@media screen and (max-width: 767px) {
  .common-head span{
    font-size: 23px;
  }
  .introHead > h1{
        font-size: 29px;
  }
}
