.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.introHead > h1 {
    font-size: 36px;
    letter-spacing: 0.07em;
}

.App-header {
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.common-head {
    margin: 0;
    padding: 13px 0;
}
/* Animation css*/



.common-head span{
    letter-spacing: 15px;
    animation: animate 1s infinite linear;
    font-size: 30px;
}

 @keyframes animate{
            0%{
                color: #ed2424;
                text-shadow: none;
            }
            18%{
                color: #ed2424;
                text-shadow: none;
            }
            20%{
                color :#ffffff;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            30%{
                color: #ed2424;
                text-shadow: none;
            }
            70%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            85%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            90%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                
            }
        }


        .common-head span:nth-child(1){
            animation-delay: .2s;
        }
         .common-head span:nth-child(2){
            animation-delay: .4s;
        }
         .common-head span:nth-child(3){
            animation-delay: .6s;
        }
         .common-head span :nth-child(4){
            animation-delay: .6s;
        }
         .common-head span :nth-child(5){
            animation-delay: .8s;
        }
         .common-head span :nth-child(6){
            animation-delay: .8s;
        }
         .common-head span :nth-child(7){
            animation-delay: .8s;
        }
         .common-head span :nth-child(8){
            animation-delay: .8s;
        }


@media screen and (max-width: 767px) {
  .common-head span{
    font-size: 23px;
  }
  .introHead > h1{
        font-size: 29px;
  }
}